import React from 'react';import classes from "./User.module.css";import {NavLink} from "react-router-dom";import userPhoto from "../../../assets/images/userPhoto.png";const User = ({user, ...props}) => {	return (		<div className={classes.wrapper} key={Math.random()}>			<div className={classes.leftside}>				<div className={classes.avatar}>					<NavLink to={`/profile/${user.id}`}>						<img src={ user.photos.small != null							? user.photos.small							: userPhoto						} alt={''}						/>					</NavLink>								</div>				<div className={classes.follow_btn}>					{						user.followed							?							<button disabled={props.followingInProgress.some(id => id === user.id)}							        onClick={() => {props.unfollow(user.id)}}							>								Unfollow							</button>							:							<button disabled={props.followingInProgress.some(id => id === user.id)}							        onClick={() => {props.follow(user.id)}							        }>								Follow							</button>					}				</div>			</div>			<div className={classes.user_info}>				<div className={classes.name_and_status}>					<div className={classes.name}>						{user.name}					</div>					<div className={classes.status}>						{user.status}					</div>				</div>				<div className={classes.location}>					<div className={classes.location_country}>						{							'Country'						}					</div>					<div className={classes.location_city}>						{							'City'						}					</div>				</div>			</div>		</div>	);};export default User;