import React from 'react';import Paginator from "../common/paginator/Paginator";import User from "./User/User";const Users = (props) => {		return (		<div>			<Paginator				totalUsersCount={props.totalUsersCount}				pageSize={props.pageSize}				onPageChanched={props.onPageChanched}			/>			{				props.usersData.map( user => <User						user={user}						followingInProgress={props.followingInProgress}						follow={props.follow}						unfollo={props.unfollow}					/>				)			}		</div>	)};export default Users;